      var rightBottom = this.$echarts.init(
        document.querySelector("#rightBottom")
      );
      var rightBottomOption = {
        color: ["#77C3F2", "#F1B000", "#ff6347", "#52CC76"],
        tooltip: {
          trigger: "item",
        },
        legend: {
          orient: "vertical",
          left: "left",
          textStyle: {
            color: "#fff",
          },
        },
        series: [
          {
            name: this.gDate,
            type: "pie",
            radius: "75%",
            data: pieData,
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
          },
        ],
      };
      rightBottom.setOption(rightBottomOption);
      window.addEventListener("resize", function () {
        rightBottom.resize();
      });
    },
    slidWindow() {
      var timer = null;
      var speed = -4;
      var od = document.getElementById("wrapper");
      var au = od.getElementsByTagName("ul")[0];
      var ali = au.getElementsByTagName("li");
      // au.innerHTML = au.innerHTML + au.innerHTML;
      // au.style.width = ali[0].offsetWidth * ali.length + 'px';
      timer = setInterval(move, 30);

      function move() {
        if (au.offsetLeft < -au.offsetWidth) {
          au.style.left = "0";
        }
        if (au.offsetLeft > 0) {
          au.style.left = -au.offsetWidth / 2 + "px";
        }

        au.style.left = au.offsetLeft + speed + "px";
      }
      od.onmouseover = function () {
        clearInterval(timer);
      };
      od.onmouseout = function () {
        timer = setInterval(move, 30);
      };
    },
    getSlids() {
      this.$req
        .fetch(API.dashboard.slid.path, {
          sDate: this.gDate,
          organizationID: this.gOrganization,
        })
        .then((resp) => {
          if (resp.code == 1) {
            this.slids = resp.data;
          }
        });
    },
  },
};
</script>

